.text-center {
  text-align: center;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Quicksand", sans-serif;
  font-size: 1.6rem;
  background-color: #ededed;
}

[class$=__container] {
  width: 95%;
  max-width: 170rem;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.header {
  padding: 0.4rem 0;
  border-bottom: 1px solid #c7c7c7;
  background-color: #ffffff;
  position: relative;
}
.header__menu {
  display: none;
}
.header__contenido {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__nav, .header__shopping {
  display: flex;
  align-items: center;
  gap: 2rem;
  position: relative;
}
.header__logo {
  transition-property: transform;
  transition-duration: 0.3s;
}
.header__logo:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.navegacion {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.navegacion__enlace {
  border: 1px solid transparent;
  padding: 1rem 1rem;
  border-radius: 0.8rem;
}
.navegacion__enlace:hover {
  border: 1px solid #f70c0c;
  color: #f70c0c;
}

.shopping {
  display: flex;
  align-items: center;
  gap: 3rem;
  position: relative;
}
.shopping__email {
  color: #000000;
  font-size: 1.4rem;
  padding: 1rem 1.5rem;
  display: flex;
  align-items: center;
  border: 1px solid #c7c7c7;
  border-radius: 1rem;
  gap: 1rem;
  margin: 0;
}
.shopping__email:hover {
  cursor: pointer;
}
.shopping__email-icon {
  background-color: #980505;
  width: 3rem;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  border-radius: 10rem;
}
.shopping__email-texto {
  display: none;
}
@media (min-width: 768px) {
  .shopping__email-texto {
    display: block;
  }
}
.shopping__cart-icon {
  width: 2.5rem;
  height: 2.5rem;
  background-image: url("../assets/icons/icon_shopping_cart.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.shopping__cart-icon:hover {
  cursor: pointer;
}
.shopping__cart-counter {
  display: block;
  width: 16px;
  height: 16px;
  position: absolute;
  right: -0.9rem;
  top: -0.9rem;
  background-color: #f70c0c;
  border-radius: 50%;
  padding: 1rem;
  font-weight: bold;
  font-size: 1.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
}

.main {
  bottom: 4.6rem;
  left: 0;
  overflow-y: scroll;
  position: absolute;
  right: 0;
  top: 6rem;
  padding-top: 1rem;
}
@media (min-width: 1024px) {
  .main {
    padding: 2rem 0;
  }
}

.login {
  width: 100%;
  height: 80vh;
  display: grid;
  place-items: center;
}
.login__contenido {
  display: grid;
  grid-template-rows: auto 1fr auto;
  width: 30rem;
}
.login__logo {
  width: 15rem;
  margin-bottom: 4.8rem;
  justify-self: center;
  display: block;
}
@media (min-width: 768px) {
  .login__logo {
    display: none;
  }
}
.login__button {
  background-color: #ffffff;
  border-radius: 0.8rem;
  border: 0.1rem solid #f70c0c;
  color: #f70c0c;
  width: 100%;
  cursor: pointer;
  font-size: 1.6rem;
  font-weight: bold;
  height: 5rem;
}

.formulario {
  display: flex;
  flex-direction: column;
}
.formulario__reset-password {
  color: #f70c0c;
  font-size: 1.4rem;
  text-align: center;
  text-decoration: none;
  margin-bottom: 5.2rem;
}
.formulario__label {
  font-size: 1.4rem;
  font-weight: bold;
  margin-bottom: 0.4rem;
}
.formulario__input {
  background-color: #f7f7f7;
  border: none;
  border-radius: 0.8rem;
  font-size: 1.6rem;
  padding: 1rem;
  margin-bottom: 1.2rem;
  color: #000000;
}
.formulario__input--email {
  margin-bottom: 2.2rem;
}
.formulario__input--select {
  color: #000;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.formulario__campo--flex-row {
  margin-top: 2rem;
  flex-direction: row;
  align-items: center;
}
@media (min-width: 1024px) {
  .formulario__campo--half {
    max-width: 50%;
  }
}
.formulario__submit {
  background-color: #f70c0c;
  border-radius: 0.8rem;
  border: 0.1rem solid transparent;
  color: #ffffff;
  width: 100%;
  cursor: pointer;
  font-size: 1.6rem;
  font-weight: bold;
  height: 5rem;
  margin-top: 1.4rem;
  margin-bottom: 3rem;
}
.formulario__submit--pay {
  margin: 0;
}
.formulario__submit--add {
  margin: 0;
  background-color: #ffffff;
  border-radius: 0.8rem;
  border: 0.1rem solid #f70c0c;
  color: #f70c0c;
  width: 100%;
  cursor: pointer;
  font-size: 1.6rem;
  font-weight: bold;
  height: 5rem;
}

.productos {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.6rem;
  place-content: center;
}
@media (min-width: 1024px) {
  .productos {
    grid-template-columns: repeat(6, 1fr);
  }
}

.producto {
  width: 100%;
  min-width: 17rem;
  max-width: 24rem;
  transition-property: transform;
  transition-duration: 0.3s;
}
.producto:hover {
  transform: scale(1.1);
  border-radius: 2rem;
}
.producto__imagen {
  width: 24rem;
  height: 24rem;
  border-radius: 2rem;
  object-fit: cover;
}
.producto__imagen:hover {
  cursor: pointer;
}
.producto__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
.producto__icon-cart {
  max-width: 3.6rem;
  display: block;
  width: 3.5rem;
  height: 3.5rem;
  background-image: url("../assets/icons/bt_add_to_cart.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.producto__icon-cart:hover {
  cursor: pointer;
}
.producto__textos {
  flex: 4;
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
}
.producto__name, .producto__price {
  margin: 0;
}
.producto__price {
  font-weight: bold;
  font-size: 1.6rem;
}
.producto__name {
  font-size: 1.4rem;
}

@media (min-width: 1024px) {
  .detail {
    padding: 1rem 20rem;
  }
}
@media (min-width: 1024px) {
  .detail__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
    align-items: center;
  }
}
.detail__image {
  width: 100%;
  border-radius: 2rem;
}
.detail__title {
  font-weight: 400;
  font-size: 3.5rem;
}
.detail__price {
  font-weight: bold;
  font-size: 4.5rem;
}
@media (min-width: 1024px) {
  .detail__description {
    grid-column: 3/6;
    padding-left: 4rem;
  }
}
@media (min-width: 1024px) {
  .detail__contenido-img {
    grid-column: 1/3;
  }
}

.characteristic {
  grid-column: 1/6;
  width: 100%;
  height: 20rem;
  border-top: 0.1rem solid #c7c7c7;
  padding: 3rem 1rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.characteristic__title {
  font-weight: bold;
  font-size: 3.5rem;
  margin: 0;
}
.characteristic__text {
  margin: 0;
}

.detail-product__title {
  font-weight: 400;
  font-size: 2.5rem;
}

.characteristics {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  margin-bottom: 2rem;
}
@media (min-width: 1024px) {
  .characteristics {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.characteristics__contenido {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.characteristics__title {
  margin: 0;
}
.characteristics__detail {
  background-color: #ffffff;
  border-radius: 1rem;
}
.characteristics__item {
  padding: 1rem 2rem;
  display: grid;
  grid-template-columns: 2fr 1fr;
  font-weight: bold;
  border-bottom: 1px solid #c7c7c7;
}
.characteristics__item:last-of-type {
  border-bottom: 1px solid transparent;
}
.characteristics__value {
  max-width: 120rem;
  font-weight: 400;
}

.shopping-my-cart {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .shopping-my-cart {
    padding: 2rem 4rem;
  }
}
.shopping-my-cart__title-container {
  width: 100%;
  display: flex;
  align-items: center;
}
.shopping-my-cart__imagen {
  transform: rotate(180deg);
  margin-right: 2.4rem;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 2rem;
  display: none;
}
@media (min-width: 1024px) {
  .shopping-my-cart__imagen {
    display: block;
  }
}
.shopping-my-cart__title {
  font-size: 2.4rem;
  font-weight: bold;
}
.shopping-my-cart__grid {
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .shopping-my-cart__grid {
    width: 90%;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(6, 1fr);
  }
}
.shopping-my-cart__menu {
  display: none;
}
@media (min-width: 1024px) {
  .shopping-my-cart__menu {
    display: block;
  }
}
@media (min-width: 1024px) {
  .shopping-my-cart__pay-contenido {
    grid-column: 2/5;
  }
}
@media (min-width: 1024px) {
  .shopping-my-cart__pay {
    grid-column: 5/7;
  }
}

.shoppings {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  column-gap: 2rem;
  max-height: 60vh;
  overflow-y: scroll;
}
@media (min-width: 1024px) {
  .shoppings {
    max-height: 70vh;
  }
}

.shopping-cart {
  display: grid;
  grid-template-columns: auto 1fr auto auto;
  gap: 1.6rem;
  margin-bottom: 1.4rem;
  align-items: center;
  background-color: #ffffff;
  padding: 1rem 3rem;
  border-radius: 1rem;
}
.shopping-cart__imagen {
  width: 7rem;
  height: 7rem;
  border-radius: 4rem;
  object-fit: cover;
}
.shopping-cart__title {
  color: #c7c7c7;
}
.shopping-cart__price {
  font-size: 1.6rem;
  font-weight: bold;
}

.order {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 1.6rem;
  align-items: center;
  background-color: #f7f7f7;
  margin-bottom: 2.4rem;
  border-radius: 0.8rem;
  padding: 0 2.4rem;
  border: 1px solid #f70c0c;
}
.order__title {
  font-size: 1.6rem;
  font-weight: bold;
}
.order__price {
  text-align: end;
  font-weight: bold;
}

.buttons-pay {
  display: flex;
  gap: 2rem;
}

.button {
  display: block;
  background-color: #f70c0c;
  border-radius: 0.8rem;
  border: 0.1rem solid transparent;
  color: #ffffff;
  width: 100%;
  cursor: pointer;
  font-size: 1.6rem;
  font-weight: bold;
  height: 5rem;
  width: 100%;
}
.button--primary {
  background-color: #ffffff;
  border-radius: 0.8rem;
  border: 0.1rem solid #f70c0c;
  color: #f70c0c;
  width: 100%;
  cursor: pointer;
  font-size: 1.6rem;
  font-weight: bold;
  height: 5rem;
}

.footer {
  width: 100%;
  border-top: 1px solid #c7c7c7;
  padding: 1.5rem;
  position: absolute;
  bottom: 0;
}
.footer__copyright {
  text-align: center;
  color: #000000;
  font-size: 1.4rem;
  margin: 0;
}